.form-checkbox {
  padding: 0;

  @media (min-width: $tablet-width) {
    padding: 50px 50px;
  }

  @media (min-width: $desktop-width) {
    padding: 48px 0 52px;
  }
}

.form-checkbox__wrapper {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 155px 1fr;
    column-gap: 12px;

    background-color: $grey;
  }

  @media (min-width: $desktop-width) {
    column-gap: 20px;

    width: 1150px;
    margin: 0 auto;
    padding: 0 50px;
  }
}

.form-checkbox__title {
  margin: 0;

  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: $green;
  text-transform: uppercase;

  @media (max-width: $mobile-max-width) {
    display: flex;
    align-items: center;

    margin-bottom: 24px;

    &::after {
      content: "";

      flex-grow: 1;
      flex-shrink: 1;
      width: 100%;
      height: 1px;
      margin-left: 23px;

      background-color: $darker-green-link;
    }
  }

  @media (min-width: $tablet-width) {
    padding-top: 17px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 20px;
    padding-left: 62px;
  }
}

.form-checkbox__list {
  margin: 0;
  padding: 0;

  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  color: $black;

  list-style: none;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: auto 1fr;
    column-gap: 63px;
    row-gap: 24px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(3, 250px);
    column-gap: 12px;
  }
}

.form-checkbox__item:not(:last-child) {
  @media (max-width: $mobile-max-width) {
    margin-bottom: 23px;
  }
}

.form-checkbox__label {
  display: flex;
  align-items: center;

  @media (min-width: $tablet-width) {
    font-size: 19px;
    line-height: 18px;
  }
}

.form-checkbox__input + .form-checkbox__label::before {
  content: "";

  box-sizing: content-box;
  flex-shrink: 0;
  width: 34px;
  height: 34px;
  margin-right: 22px;

  background-color: $white;
  border: 2px solid $lighter-black;

  @media (min-width: $tablet-width) {
    width: 48px;
    height: 48px;
    margin-right: 30px;

    background-color: transparent;
  }
}

.form-checkbox__input:checked + .form-checkbox__label::before {
  background-image: url("../img/icons/tick-mobile.svg");
  background-repeat: no-repeat;
  background-position: center;

  @media (min-width: $tablet-width) {
    background-image: url("../img/icons/tick.svg");
  }
}

.form-checkbox__input:focus + .form-checkbox__label::before {
  border: 2px solid $darker-green-link;
}

.form-checkbox__input + .form-checkbox__label:hover::before {
  border: 2px solid $darker-green-link;
}

.form-checkbox__input + .form-checkbox__label:active::before {
  border: 2px solid $lighter-black-input;
}
